header.header,
.footer-mylt {
  display: none;
}

.nbr_navigation-bar {
  position: fixed;
  top: 0;
  height: 80px;
  width: 100%;
  padding: 0 48px;
  z-index: 100;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  background: #698bb9;
  background: linear-gradient(90deg, #698bb9 0%, #224a85 100%);
}
.nbr_navigation-bar .nbr_nav-button {
  background: none;
  border: none;
  border-radius: 4px;
}
.nbr_navigation-bar .nbr_nav-button:hover, .nbr_navigation-bar .nbr_nav-button:focus {
  background: rgba(57, 112, 183, 0.25);
}
.nbr_navigation-bar .nbr_nav-button svg {
  height: 21px;
  margin: 8px 4px;
  vertical-align: middle;
}
.nbr_navigation-bar .nbr_lt-logo {
  height: 26px;
}
.nbr_navigation-bar .nbr_member-link {
  margin: 0 18px;
}
.nbr_navigation-bar .nbr_member-link svg {
  border: 1px solid #fff;
  border-radius: 50%;
  height: 30px;
  vertical-align: middle;
  background: linear-gradient(90deg, #fff 50%, transparent 0);
  background-position: 100% 100%;
  background-size: 200% 100%;
  color: #fff;
  transition: all 0.5s ease;
}
.nbr_navigation-bar .nbr_member-link svg:hover {
  background-position: 0 100%;
  color: #255891;
}
.nbr_navigation-bar .nbr_nav-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 120;
  width: 639px;
  max-width: 100%;
  background: #5277ab;
  background: linear-gradient(140deg, #5277ab 0%, #1f467d 100%);
  visibility: hidden;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-header {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 48px 72px;
  border-bottom: 1px solid #e8e8e8;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-header .nbr_lt-logo {
  height: 31px;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-header button {
  border: none;
  background: none;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-header button svg {
  height: 25px;
  vertical-align: middle;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body {
  padding: 40px 72px;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body ul {
  list-style-type: none;
  padding: 0;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body ul li {
  padding: 8px 0;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body ul a {
  color: #fff;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body ul a:hover {
  color: #00cbed;
}
.nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body .nbr_lt-brand-list li {
  font-size: 24px;
  font-weight: 300;
}
.nbr_navigation-bar .nbr_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 110;
  background: #000;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.4s ease;
}

@media only screen and (max-width: 992px) {
  .nbr_navigation-bar {
    background: #25569a;
    padding: 0 24px;
  }
  .nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-header {
    padding: 32px 32px 32px 57px;
  }
  .nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-header .nbr_lt-logo {
    -webkit-flex-grow: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
  }
  .nbr_navigation-bar .nbr_nav-menu .nbr_nav-menu-body {
    padding: 32px;
  }
}